.about {
  background-color: hsl(162deg 100% 29.53%);
  min-height: 110vh;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.aboutCard {
  height: 100%;
  width: 80%;
  background-color: white;
  position: relative;
  padding-top: 1rem 0 1rem 0;
  transform: translateX(-100vw);
  animation: aboutCardAnimation 1s ease-in forwards;
}

.aboutCard > div:nth-child(1) {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  transform: translateY(-100vh);
  animation: topAnimation 1.5s ease-in forwards;
}
.left {
  border-top: 3px solid #bbb;
  width: 29.5%;
}
.right {
  border-top: 3px solid #bbb;
  width: 29.5%;
}
.aboutCard > div:nth-child(2) {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 2rem 1rem;
}
.aboutCard > div:nth-child(2) > div {
  width: 50%;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  color: #685050;
  font-size: 1rem;
  line-height: 2.5;
  letter-spacing: 0.00938em;
  text-align: center;
  font-weight: 600;
}
.aboutImg {
  text-align: center;
}
.aboutImg > img {
  border-radius: 20%;
  transition: all 0.5s;
  width: 50%;
}
.aboutImg > img:hover {
  transform: scale(1.03);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3457);
}
.bottom {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.love {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem 1rem;
  width: 50%;
}
.love > h3 {
  font-size: 2rem;
  font-weight: bolder;
  font-family: system-ui;
  color: #282626;
}
.likes {
  display: flex;
}
.likes > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1rem 1rem;
}
.loveImg {
  width: 3rem;
  transition: all 0.5s;
}
.loveImg:hover {
  transform: scale(1.2);
}
span {
  font-style: italic;
  color: #282626;
}
.work {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 2rem 1rem;
  width: 50%;
}
.work > h3 {
  font-size: 2rem;
  font-weight: bolder;
  font-family: system-ui;
  color: #282626;
  margin-top: 2rem;
}
.work > div {
  display: flex;
  margin-top: 1rem;
}

.work > div > img {
  width: 1rem;
  margin-right: 0.5rem;
}
.work > div > p {
  font-weight: 700;
  font-style: italic;
  color: rgb(168, 196, 66);
}

@media screen and (max-width: 864px) {
  .aboutCard > div:nth-child(2) > div {
    width: 100%;
  }

  .aboutCard > div:nth-child(2) {
    flex-direction: column;
  }
  .work > h3,
  .love > h3 {
    font-size: 1rem;
    color: #7c5f5f;
  }
}
@media screen and (max-width: 544px) {
  .likes {
    flex-direction: column;
  }

  .work > div {
    margin: 2rem 0rem;
  }
  .aboutCard {
    margin-bottom: 1rem;
  }
}
@keyframes aboutCardAnimation {
  to {
    transform: translateX(0%);
  }
}

@keyframes topAnimation {
  to {
    transform: translateY(0%);
  }
}
